.parallaxChildren {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-left: 20em;
  display: block;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
}

.bannerContainer {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.bannerBg {
  background: #fff;
}

.customChild {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  text-align: center;
}

@media only screen and (max-width: 600px) {
  .parallaxChildren {
    padding-left: 0;
    padding-top: 3em;
  }
}
